#footer {
  width: 100%;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  .footer-wrapper {
    width: 720px;
    .footer-bottom {
      width: 720px;
      display: flex;
      justify-content: space-between;
      color: #999;
      font-size: 10px;
      border-top: 1px solid #ccc;
      padding: 10px 0px
    }
  }
  .container {
    width: 100%;
    display: flex;
    .asistent-container {
      display: flex;
      flex-direction: row;
      width: 380px;
     
    }
    .suport-container {
      width: 339px;
      .footer-item-number {
        margin-bottom: 10px;
      }
      .footer-item {
        a {
          text-decoration: underline !important;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    .footer-item.width {
      width: 100% !important;
      position: relative;
      img {
        width: 100px;
        position: absolute;
        right: 0px;
        bottom: 5px;
      }
    }
    .footer-item {
      min-height: 122px;
      font-size: 10px;
      color: #707070;
      text-align: left;
      width: 40%;
      strong {
        text-shadow: 0 1px 0 #fff;
        font-size: 16px;
        font-weight: bold;
        color: #343434;
      }
      .footer-item-title {
        margin-bottom: 10px;
      }

      .footer-item-description {
        margin-bottom: 5px;
      }
      a {
        color: #707070;
        text-decoration: none;
        &:hover {
          text-decoration: none;
          color: #707070;
        }
      }
    }
  }
}